@import url('https://fonts.googleapis.com/css2?family=Cabin&family=Roboto:wght@100&family=Rubik&display=swap');

*{
  box-sizing: border-box;
  font-family: 'Cabin', sans-serif;
  font-family: 'Roboto', sans-serif;
  font-family: 'Rubik', sans-serif;
}

body {
  background-color: whitesmoke;
  display : flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
}

.container {
  margin : 30px auto;
  width: 350px;

}

.horizontal-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: gray ;
  text-align: center;

}
.horizontal-menu li {
  display: inline-block;
}

.horizontal-menu a {
  color: white;
  text-decoration: none;
  padding: 16px;
  display: block;


}