*{
  box-sizing: border-box;
  font-family: 'Cabin', sans-serif;
  font-family: 'Roboto', sans-serif;
  font-family: 'Rubik', sans-serif;
}

body {
  background-color: whitesmoke;
  display : flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
}

.container {
  margin : 30px auto;
  width: 350px;

}

.horizontal-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: gray ;
  text-align: center;

}
.horizontal-menu li {
  display: inline-block;
}

.horizontal-menu a {
  color: white;
  text-decoration: none;
  padding: 16px;
  display: block;


}
.report-container {
    display: flex;
    justify-content: space-between;
    background-color: blanchedalmond;
    padding: 15px;
    margin: 15px 0;
}

.report-container >div {
    flex: 1 1;
    text-align: center;
}

.report-container >div:first-of-type {
    border-right: 1px solid #ccc;

}

.report {
    font-size: 20px;
    letter-spacing: 1px;
    margin: 5px 0;
}

.report.totalincome {
    color: mediumseagreen;

}

.report.totaloutcome {
    color: firebrick;
}
